.center{
  text-align:center;
  color:white;
  text-shadow: 4px 0 0 #000, 0 -1px 0 #000, 0 1px 0 #000, -1px 0 0 #000;
}



.sticky{position:sticky;top:0;background:white;}
.nav{
  display:inline;
  margin-left: 10px;
  color:black;
  
}
.nav:hover{background:grey;color:white;}
#links{

 
  list-style-type: none;
 
  padding: 0;
  width: 100%;
  background-color:white;

}
li{
  display: block;
  color: #000;
  padding: 8px 16px;
 
  color:black;
  border:1px solid black;
 

}
li:hover{
  background: grey;
  color:white;

}
Link{
  text-decoration: none;
}

